import { Component, Event, EventEmitter, Fragment, Host, State, h } from '@stencil/core';
import { Locale } from 'date-fns';
import { ICurrency, IExposedLanguages, pages } from '@/models/common';
import { IExposedProperty, Variation } from '@/models/property';
import booking_store, { calculateTotalCost, getVisibleInventory, reserveRooms, updateRoomParams } from '@/stores/booking';
import app_store, { onAppDataChange } from '@/stores/app.store';
import { cn, formatAmount, getDateDifference } from '@/utils/utils';
// import { roomtypes } from '@/data';
@Component({
  tag: 'ir-booking-page',
  styleUrl: 'ir-booking-page.css',
  shadow: true,
})
export class IrBookingPage {
  @State() selectedLocale: Locale;
  @State() property: IExposedProperty;
  @State() currencies: ICurrency[];
  @State() languages: IExposedLanguages[];
  @Event() routing: EventEmitter<pages>;

  componentWillLoad() {
    this.property = { ...app_store.property };
    onAppDataChange('property', (newValue: IExposedProperty) => {
      console.log(newValue);
      this.property = { ...newValue };
    });
  }

  handleVariationChange(e: CustomEvent, variations: Variation[], rateplanId: number, roomTypeId: number) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    const value = e.detail;
    const selectedVariation = variations.find(variation => variation.adult_child_offering === value);
    if (!selectedVariation) {
      return;
    }
    updateRoomParams({ params: { selected_variation: selectedVariation }, ratePlanId: rateplanId, roomTypeId });
  }
  render() {
    return (
      <Host>
        <div class="space-y-5">
          <div>
            <ir-property-gallery exposed_property={this.property}></ir-property-gallery>
          </div>
          <div>
            <ir-availibility-header></ir-availibility-header>
          </div>

          <section class="relative rounded-md gap-4 justify-between ">
            <div class="divide-y flex-1 py-2">
              {booking_store.roomTypes?.map(roomType => {
                if (!roomType.is_active || roomType.images.length <= 0 || (roomType.inventory <= 0 && booking_store.enableBooking)) {
                  return null;
                }
                return (
                  <section class="flex flex-col  justify-start gap-4 mb-4   md:flex-row">
                    <aside class="hidden md:block">
                      <ir-property-gallery property_state="carousel" roomType={roomType} exposed_property={this.property}></ir-property-gallery>
                    </aside>
                    <div class="space-y-1 w-full flex-1 py-2">
                      <h3 class="text-slate-900  font-medium text-lg ">{roomType.name}</h3>
                      {/* Mobile view for carousel */}
                      <div class="md:hidden">
                        <ir-property-gallery property_state="carousel" roomType={roomType} exposed_property={this.property}></ir-property-gallery>
                      </div>
                      <div class="hidden lg:block">
                        <ir-accomodations
                          bookingAttributes={{
                            max_occupancy: roomType.occupancy_max.adult_nbr,
                            bedding_setup: roomType.bedding_setup,
                          }}
                          amenities={this.property.amenities}
                        ></ir-accomodations>
                      </div>

                      {booking_store.enableBooking ? (
                        roomType.rateplans.map(ratePlan => {
                          if (!ratePlan.is_active) {
                            return null;
                          }

                          if (!ratePlan.variations) {
                            return null;
                          }
                          const visibleInventory = getVisibleInventory(roomType.id, ratePlan.id);
                          return (
                            <div key={ratePlan.id} class="bg-gray-100 app_container w-full p-2 flex flex-col lg:grid lg:grid-cols-7 lg:gap-4 space-y-1 text-sm">
                              <div
                                class={cn('flex justify-between md:justify-start w-full md:w-fit', {
                                  'lg:col-span-1 xl:col-span-2': !ratePlan.custom_text,
                                })}
                              >
                                <p class="line-clamp-3 font-semibold">
                                  {ratePlan.short_name}
                                  <span class="hidden md:inline text-slate-700 text-sm">{ratePlan.custom_text}</span>
                                </p>
                                {visibleInventory.reserved > 0 && (
                                  <div class="flex items-start gap-1 md:hidden">
                                    <p class="font-medium">{formatAmount(visibleInventory.selected_variation?.amount, app_store.userPreferences.currency_id)}</p>
                                    <p class="font-medium line-through text-red-500">
                                      {formatAmount(visibleInventory.selected_variation?.total_before_discount, app_store.userPreferences.currency_id)}
                                    </p>
                                  </div>
                                )}
                              </div>

                              <p class="md:hidden line-clamp-3 text-slate-700 text-xs">{ratePlan.custom_text}</p>

                              <div
                                class={cn(
                                  'w-full space-y-2  flex flex-col md:space-y-0  md:justify-between md:flex-row md:items-start lg:grid lg:grid-cols-5 lg:col-span-5 lg:gap-4',
                                  {
                                    'lg:col-span-6 xl:col-span-5': !ratePlan.custom_text,
                                    'md:justify-normal md:gap-4': visibleInventory.reserved === 0,
                                  },
                                )}
                              >
                                <div
                                  class={cn('lg:col-span-2', {
                                    'md:w-full': visibleInventory.reserved === 0,
                                  })}
                                >
                                  <ir-select
                                    class="w-full md:w-auto"
                                    label="Travelers"
                                    value={visibleInventory.selected_variation?.adult_child_offering}
                                    onValueChange={e => this.handleVariationChange(e, ratePlan.variations, ratePlan.id, roomType.id)}
                                    data={ratePlan.variations.map(v => ({
                                      id: v.adult_child_offering,
                                      value: `${v.adult_nbr} adults - ${v.child_nbr} children`,
                                    }))}
                                  ></ir-select>
                                  <div class="hidden md:flex items-center gap-4">
                                    <p>Cancelation conditions</p>
                                    <ir-tooltip class="text-gray-700 flex items-center justify-center" message={ratePlan.cancelation}></ir-tooltip>
                                  </div>
                                </div>

                                {visibleInventory.reserved > 0 && (
                                  <div class="hidden md:flex items-center gap-1 flex-col justify-between">
                                    <Fragment>
                                      <p class="font-medium line-through">
                                        {formatAmount(visibleInventory.selected_variation?.total_before_discount, app_store.userPreferences.currency_id)}
                                      </p>
                                      <p class="font-medium  text-red-500">%{visibleInventory.selected_variation?.discount_pct}</p>
                                    </Fragment>
                                  </div>
                                )}
                                {visibleInventory.reserved > 0 && (
                                  <div class="hidden md:flex items-center  flex-col justify-between">
                                    <Fragment>
                                      <p class="font-medium text-lg">{formatAmount(visibleInventory.selected_variation?.amount, app_store.userPreferences.currency_id)}</p>
                                      <p class="font-medium text-xs">{visibleInventory.selected_variation?.amount_per_night}</p>
                                    </Fragment>
                                  </div>
                                )}
                                <ir-select
                                  onValueChange={e => reserveRooms(roomType.id, ratePlan.id, Number(e.detail))}
                                  label="Rooms"
                                  value={visibleInventory.reserved}
                                  class={cn('w-full  md:w-auto', {
                                    'md:w-full lg:col-span-2': visibleInventory.reserved === 0,
                                  })}
                                  data={[...new Array(roomType.inventory + 1)]?.map((_, i) => ({
                                    id: i,
                                    value: `${i}&nbsp;&nbsp;&nbsp;${
                                      i === 0 ? '' : formatAmount(visibleInventory.selected_variation?.amount * i, app_store.userPreferences.currency_id)
                                    }`,
                                    disabled: i >= visibleInventory.visibleInventory + 1,
                                    html: true,
                                  }))}
                                ></ir-select>
                              </div>
                            </div>
                          );
                        })
                      ) : (
                        <div class="bg-gray-100 app_container w-full p-2 flex-1  flex flex-col md:flex-row justify-between space-y-1 text-sm rounded-[var(--radius,8px)]">
                          <p>{roomType.description}</p>
                        </div>
                      )}
                    </div>
                  </section>
                );
              })}
            </div>
          </section>
          <section class={'text-sm'}>
            <h2 class="text-lg font-medium mb-5">Facilities and services</h2>
            <ir-facilities properties={this.property}></ir-facilities>
            <p innerHTML={this.property?.description.location_and_intro} class="py-2"></p>
          </section>
        </div>
        {booking_store.enableBooking && calculateTotalCost() > 0 && (
          <div class="z-40 text-lg mt-14 lg:text-2xl rounded-md  sticky bottom-2 text-gray-200 gap-10  flex w-full items-center justify-end  bg-gray-700/80">
            <div class="">
              <p>{getDateDifference(booking_store.bookingAvailabilityParams.from_date ?? new Date(), booking_store.bookingAvailabilityParams.to_date ?? new Date())} nights</p>
            </div>
            {calculateTotalCost() > 0 && <div>{formatAmount(calculateTotalCost(), app_store.userPreferences.currency_id)}</div>}
            <ir-button
              onButtonClick={() => this.routing.emit('checkout')}
              label="BOOK NOW"
              size="lg"
              class="w-60"
              buttonStyles={{
                height: '64px',
                borderRadius: '0',
                borderTopRightRadius: app_store.dir === 'RTL' ? '0px' : '6px',
                borderBottomRightRadius: app_store.dir === 'RTL' ? '0px' : '6px',
                borderTopLeftRadius: app_store.dir === 'RTL' ? '6px' : '0px',
                borderBottomLeftRadius: app_store.dir === 'RTL' ? '6px' : '0px',
              }}
            ></ir-button>
          </div>
        )}
      </Host>
    );
  }
}
